<template>
  <div class="app" @click="onClick">
    <img v-if="app.icon_uri && !iconFailedToLoad" class="icon" :src="app.icon_uri" @error="iconFailedToLoad = true" />
    <div v-else class="icon icon_stub"></div>

    <div class="content">
      <div class="main-line">
        <div class="name-container">
          <button class="filterable name ellipsis" @click="updateFilters('name')">{{ app.name }}</button>
          <sup class="jit-badge" v-if="app.use_jit_support">JIT</sup>
        </div>
        <span class="version">{{ app.version }}</span>
      </div>

      <div class="source-line ellipsis">
        <button class="filterable ellipsis" @click="updateFilters('developer_name')">{{
          app.developer_name }}</button>

      </div>

      <div class="sub-line">
        <button class="filterable source-link ellipsis" @click="updateFilters('source_name')">{{ app.source_name
          }}</button>

        <div class="metadata">
          <div class="detail-item">
            <button class="filterable ellipsis" @click="updateFilters('type')">{{ $t(app.type?.toUpperCase())
              }}</button>
          </div>
          <div v-if="app.genre_id" class="detail-item">
            <button class="filterable ellipsis" @click="updateFilters('genre_id')">{{ genre }}</button>
          </div>
        </div>
      </div>
    </div>

    <div v-if="$scopedSlots['custom-action']" class="actions">
      <slot name="custom-action" :app="app" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { operatingSystems } from '@/data';
import installApp from '@/mixins/installApp';

export default {
  mixins: [installApp],
  props: {
    app: Object,
    shareable: Boolean,
  },
  data() {
    return {
      iconFailedToLoad: false,
      isInstalling: false,
    };
  },
  computed: {
    ...mapState(['genres']),
    genre() {
      return Object.values(this.genres.official)
        .find(genre => genre.id === this.app.genre_id)?.name;
    },
    compatibleOperatingSystems() {
      return operatingSystems.filter(({ name }) => this.app[`min_${name}_version`]);
    },
  },
  methods: {
    updateFilters(filter) {
      if (typeof filter === 'string') {
        this.$emit('update-filters', { [filter]: this.app[filter] });
        return;
      }

      this.$emit('update-filters', filter);
    },
    onClick(e) {
      if (e.target.closest('button')) {
        return;
      }

      this.$router.push(`/details/${this.app.universal_object_identifier}`);
    },
    async install() {
      if (this.app.no_installation_ticket_failure_reason) {
        this.$notify({
          type: 'error',
          text: this.app.no_installation_ticket_failure_reason.translated,
        });
        return;
      }

      this.isInstalling = true;
      try {
        await this.installApp(this.app.object.id, 'universal', { installationTicket: this.app.installation_ticket });
      } finally {
        this.isInstalling = false;
      }
    },
  },
};
</script>

